import React, {Component} from "react";

import Utils from '../../utils/Utils';

const utils = new Utils();

export default function Agent(props){
    return <div>
                <div className="row">
                    <div className="col-md-7 col-sm-7">
                        <h4>{props.agent.firstname} {props.agent.lastname}</h4>
                        <h5>{utils.getDate(props.agent.created)}</h5>
                    </div>
                    <div className="col-md-5 col-sm-5">
                        <h4>{props.agent.phone_number}</h4>
                        <h4>{props.agent.department}</h4>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-offset-6 col-md-6 col-sm-offset-6 col-sm-6">
                        <button onClick={props.deleteClicked(props.agent)} className="laserbtn laset-red-bg laser-inline">delete</button>
                        <button onClick={props.editClicked(props.agent)} className="laserbtn laset-blue-bg laser-inline">edit</button>
                    </div>
                </div>
           </div>
} 